.loading-div {
  display: flex;
  height: 600px;

  .loading {
    margin: auto;
  }
}

.index-next-wrap {
  width: 100%;
  height: 100px;
  //margin: 10px 0;
  background: #fff;
  display: flex;
  cursor: pointer;


  .next-icon {
    font-size: 50px;
    color: #8590a6;
    margin: auto;
    transition: .5s;

  }

  &:hover .next-icon {
    color: #0066ff;
    font-size: 70px;
  }
}