.index-item {
  //height: 260px;
  border-bottom: 1px solid #f0f2f7;
  padding: 16px 20px;

  a {
    text-decoration: none;
  }

  .author-info {
    display: flex;
    align-items: center;

    height: 30px;

    .avatar {
      width: 30px;
      height: 30px;
    }

    .login-name {
      margin-left: 10px;
      font-size: 15px;
      font-weight: 500;
      color: #444;
    }
  }

  .title {
    font-size: 18px;
    color: #121212;
    font-weight: 600;
    margin-top: 10px;
  }

  .content {
    font-size: 15px;
    color: #121212;
  }

  .count-info {
    display: flex;
    align-items: center;
    margin-top: 12px;
    color: #8590a6;
    font-size: 14px;

    .num {
      margin-right: 10px;
      margin-left: 3px;
    }

    .time {
      margin-left: auto;
    }
  }
}