$headerHeight: 52px;

html, body  {
  margin: 0;
  padding: 0;

  height: 100%;
  width: 100%;

}

.headerWrap {

  //border-bottom: 1px solid #8590a6;
  background-color: #fff;
  position: fixed;
  top: 0px;
  width: 100%;
  height: $headerHeight;
  z-index: 999;

  .header {

    width: auto;
    margin: 0 auto;
    max-width: 1156px;
    min-width: 1000px;
    height: $headerHeight;

    overflow: hidden;
    background: #fff;
    //background: skyblue;

    .logo {
      width: 100px;
      height: 100%;
      //border: 1px solid seagreen;
      float: left;
      margin-left: 10px;

      span {
        font-size: 25px;
        color: #06f;
        line-height: $headerHeight;
        font-weight: 600;
        //padding: 20px;
      }
    }

    .nav {
      color: #8590a6;
      text-decoration: none;
      font-size: 15px;
      width: 30px;
      line-height: 49px;
      position: relative;
      float: left;
      box-sizing: border-box;
      margin-right: 30px;

      &:hover {
        color: #121212;
      }

      span {
        position: relative;
        top: 2px;
      }
    }



    .nav-active {
      color: #121212;
      font-weight: 600;
      border-bottom: 4px solid #06f;
    }

    .search {
      box-sizing: border-box;
      background: #f6f6f6;
      width: 400px;
      height: 34px;
      border: 1px solid #ebebeb;
      float: left;
      margin: 9px 0;
      margin-left: 100px;

      border-radius: 999px;


      input {
        border: none;
        outline:none;

        height: 30px;
        width: 350px;
        background: transparent;

        margin-left: 20px;

        &:focus {
          //outline:none;
        }
      }

      .icon {
        width: 20px;
        position: relative;
        //top: -10px;
      }
    }

    .avatar {
      width: 30px;
      height: 30px;
      margin-top: 11px;
      float: right;

      background-color: seagreen;
    }

    .message {
      float: right;
      margin-top: 14px;
      margin-right: 30px;
    }
  }
}


