.topic-content-wrap {
  padding: 0px 10px;

  img {
    max-width: 650px;
    //margin: 0 auto;
  }

  .title {
    font-size: 24px;
    line-height: 1.22;
    font-weight: 600;
    color: #121212;
    margin: 20px 0;
  }


  .topic-author-count-info {
    display: flex;
    align-items: center;

    .topic-author-info {

      height: 38px;
      display: flex;
      align-items: center;

      .topic-author-avatar {
        width: 30px;
        height: 30px;
      }

      .topic-login-name {
        font-size: 15px;
        color: #444;
        font-weight: 600;
        margin-left: 10px;
      }

      .topic-tag {
        margin-left: 20px;
      }
    }

    .topic-count-info {
      margin-left: auto;

      span {
        margin-right: 8px;
      }

    }
  }

  .topic-content {
    padding: 8px 5px;
    font-size: 15px;
  }

  .topic-comment {
    margin-top: 10px;
    //border-top: 1px solid #f0f0f0;
    word-wrap: break-word !important;
    max-width: 650px;



    .ant-list-header {
      font-size: 18px;
      font-weight: 500;
    }


    .ant-comment-content {
      font-size: 15px;
      padding: 0 0 10px 0;
      border-bottom: 1px solid #f6f6f6;

      img {
        max-width: 580px;
        margin: 10px 0;
      }
    }

    .ant-comment-content-detail {
      margin-top: 6px;
    }
  }

  .ant-comment-content-author-name {
    color: #121212;
    font-size: 14px;

    a {
      color: #121212;
      font-weight: 500;
    }
  }

  .ant-comment-content-author-time {
    color: #494949;
  }
}