html, body  {
  margin: 0;
  padding: 0;

  height: 100%;
  width: 100%;
}

.index-wrap {
  position: relative;
  top: 52px;
}

.main-container {

  width: 1000px;
  height: 100%;
  //padding: 0 16px;
  margin: 0 auto;

  margin-top: 10px;

  display: flex;
  align-items: flex-start;

  box-sizing: border-box;
  //border: 1px solid crimson;


  >.content {
    //height: 1000px;
    width: 690px;

    margin: 0 auto;
    background-color: #fff;
    //border: 1px solid seagreen;





    .nav-wrap {
      width: 100%;
      height: 60px;
      padding-left: 30px;
      //border: 1px solid seagreen;

      color: #8590a6;
      border-bottom: 1px solid #f0f2f7;


      .nav {
        line-height: 60px;
        font-size: 16px;
        color: #121212;
        width: 58px;
        display: inline-block;
        text-decoration: none;
        font-weight: 500;
      }

      .nav-active {
        color: #0066ff;
      }

    }
  }

  .global-side-bar {
    flex: 1 1;
    //border: 1px solid saddlebrown;
    //height: 1000px;
    background-color: #fff;
  }


}